body {
  margin: 0;
  font-family: "Karla", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

header{
  background: linear-gradient(90deg, #672280 1.18%, #A626D3 100%);
  padding:20px;
  color:white;
  display: flex;
  justify-content: center;
}

.header--div{
  display: flex;
  justify-content: space-between;
  width: 1300px;
}

.header--title{
  margin-left: 2rem;
  font-size: 1.25rem;
}

.header--project{
  margin-right: 2rem;
}

main{
  padding: 3rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.form{
  display: grid;
  grid-template: 40px 40px / 1fr 1fr;
  gap: 17px;
  width: 1000px;
}

.form--input{
  font-family: "Karla", sans-serif;
  border-radius: 5px;
  border: 1px solid #D5D4D8;
  text-indent: 5px;
}

.form--button{
  grid-column: 1/-1;
  font-family: "Karla", sans-serif;
  border-radius:5px;
  background: linear-gradient(90.41deg, #711F8D 1.14%, #A818DA 100%);
  color: white;
  border:none;
  cursor: pointer;
}

.meme--img{
  max-width: 100%;
  border-radius: 3px;
}

.meme{
  position: relative;
  margin-top: 1rem;
}

.meme--text {
  position: absolute;
  width: 80%;
  text-align: center;
  left: 50%;
  transform: translateX(-50%);
  margin: 15px 0;
  padding: 0 5px;
  font-family: impact, sans-serif;
  font-size: 2em;
  text-transform: uppercase;
  color: white;
  letter-spacing: 1px;
  text-shadow:
      2px 2px 0 #000,
      -2px -2px 0 #000,
      2px -2px 0 #000,
      -2px 2px 0 #000,
      0 2px 0 #000,
      2px 0 0 #000,
      0 -2px 0 #000,
      -2px 0 0 #000,
      2px 2px 5px #000;
}

.bottom {
  bottom: 0;
}

.top {
  top: 0;
}